body {
  width: 100%;
  height: 100%;
  background-color: hsl(204, 5%, 19%)
}

.containers {
  display: flex;
}

.letters {
  fill: rgba(235, 235, 235, 0.863);
  font-size: 70px;
  font-family: monospace;
  cursor: pointer;
}

.lettersnu {
  fill: rgba(235, 235, 235, 0.863);
  font-size: 80px;
  font-family: monospace;
  cursor: pointer;
}

.textUnderLetters {
  fill: rgba(235, 235, 235, 0.863);
  font-size: 15px; 
  font-family: monospace;
  cursor: pointer;
}

.higgsBoson {
  --saturation: 31%;
  fill:hsl(178, var(--saturation), 40%)
}

.quarks {
  --saturation: 71%;
  fill: hsl(16, var(--saturation), 66%);
}

.leptons {
  --saturation: 75%;
  fill:hsl(45, var(--saturation), 58%);
}

.bosons {
  --saturation: 52%;
  fill:hsl(8, var(--saturation), 45%);
}

.higgsBoson, .quarks, .leptons, .bosons {
  stroke-width: 6px;
  stroke: hsl(240, 1%, 19%);
}

.forms {
  margin: 40px 100px 0px 100px;
  height: 600px;
  width: 600px;  
  background-color: hsl(204, 5%, 19%);
}

.everySvg:hover text {
  fill :rgba(0, 0, 0, 0.521);
}

polygon {
  cursor: pointer;
}

.standartModel {
  height: 100%;
  width: 800px;
  background-color: hsl(204, 5%, 19%);
}

.description {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(180, 179, 179);
  background-color: hsl(204, 5%, 19%);
  height: 100%;
  width: 500px;
  flex-shrink: 0;
  padding-top: 60px;
  padding-right: 150px;
  letter-spacing: 1px;
}

h1 {
  font-size: 21px;
  border-bottom: 2px dotted gray;
  padding-bottom: 20px;
  color: rgba(0, 247, 255, 0.596);
}

.generation {
  visibility: hidden;
}

.mainText {
  padding-top: 40px;
  line-height: 20px;
  height: 400px;
}

.particleDescription {
  padding-top: 40px;
  height: 400px;
  animation: changeOpacity 1s;
 
}

@keyframes changeOpacity {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

.details {
  display: flex;
  justify-content: space-between;
}

.legend {
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

